'esversion: 6';
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue';
import 'element-theme-dark';

Vue.use(ElementUI);

function formatNumber(s, n) {
	n = n > 0 && n <= 20 ? n : 2;
	s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
	let l = s.split(".")[0].split("").reverse(),
		r = s.split(".")[1];
	let t = "";
	for (let i = 0; i < l.length; i++) {
		t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
	}
	return t.split("").reverse().join("") + "." + r;
}

class Backend {
	constructor() {
		this.interval = null;
		this.backendConnected = false;
		this.colors = [];
		this.taskStatus = "stopped";
		this.current = {};
		this.results = [];
		this.diffResults = [];
		this.tableData = [];
		this.backendAddress = "127.0.0.1";
		this.backendPort = 10870;
		this.backendDest = "http://" + this.backendAddress + ":" + this.backendPort;
		this.vue = null;
		this.updateBackendDest = this.updateBackendDest.bind(this);
		this.checkBackendConnection = this.checkBackendConnection.bind(this);
		this.getUpdates = this.getUpdates.bind(this);
		this.updateTable = this.updateTable.bind(this);
		this.getDiff = this.getDiff.bind(this);
		this.updateConfigTable = this.updateConfigTable.bind(this);
	}

	init(vue) {
		this.vue = vue;
		this.checkBackendConnection();
		setInterval(this.checkBackendConnection, 10000);
	}

	setBackendAddr(addr,port){
		this.backendAddress = addr;
		this.backendPort = port;
		this.updateBackendDest();
		this.vue.notify(
			"Success",
			"success",
			"Backend address set as \n" + this.backendDest,
			3000
		);
	}

	updateBackendDest(){
		this.backendDest = "http://" + this.backendAddress + ":" + this.backendPort;
	}

	getVersion(){
		var Http = new XMLHttpRequest();
		Http.open("GET", this.backendDest + "/getversion");
		Http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
		Http.send();
		let _this = this;
		Http.onreadystatechange = function () {
			if (this.readyState == 4 && this.status == 200) {
				let rep;
				try{
					rep = JSON.parse(Http.responseText);
				}catch(e){
					console.log(e);
					_this.notify("Error","error","Read version failed.");
					return;
				}
				_this.vue.mainVersion = rep.main;
				_this.vue.webApiVersion = rep.webapi;
			} else if (this.readyState == 4 && this.status == 0) {
				_this.notify("Error","error","Read version failed.");
				return;
			}
		};
	}

	checkBackendConnection() {
		var Http = new XMLHttpRequest();
		Http.open("GET", this.backendDest + "/status");
		Http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
		Http.send();
		let _this = this;
		Http.onreadystatechange = function () {
			if (this.readyState == 4 && this.status == 200) {
				if (_this.backendConnected == false) {
					_this.backendConnected = true;
					_this.vue.backendConnected = true;
					_this.vue.$notify(
						{
							title: "Status Updated",
							type: "success",
							message: "Backend connected.",
							duration: 3000
						}
					);
					_this.readColors();
				}
			} else if (this.readyState == 4 && this.status == 0) {
				_this.vue.backendConnected = false;
				_this.backendConnected = false;
				_this.vue.$notify(
					{
						title: "Status Updated",
						type: "error",
						message: "Connect to backend failed.Retry in 10s.",
						duration: 3000
					}
				);
			}
		}
	}

	readColors() {
		this.vue.readColorsBtnLoading = true;
		var Http = new XMLHttpRequest();
		Http.open("GET", this.backendDest + "/getcolors");
		Http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
		Http.send();
		console.log("Get colors request sent.");
		let _this = this;
		Http.onreadystatechange = function () {
			if (this.readyState == 4 && this.status == 200) {
				_this.vue.readColorsBtnLoading = false;
				let colors = [];
				try {
					colors = JSON.parse(Http.responseText);
				} catch (e) {
					console.log(e);
					_this.vue.notify("Colors", "error", "Read colors failed.", 3000);
					return;
				}
				_this.colors = [];
				for (let i = 0; i < colors.length; i++) {
					_this.colors.push(
						{
							value: colors[i].name,
							label: colors[i].name,
							data: colors[i].colors
						}
					);
				}
				_this.vue.resultColors = _this.colors;
			} else if (this.readyState == 4 && this.status == 0) {
				_this.vue.notify("Colors", "error", "Read colors failed.", 3000);
				_this.vue.readColorsBtnLoading = false;
			}
		};
	}

	start(_data) {
		this.vue.startBtnLoading = true;
		this.tableData = this.vue.resultTableData;
		var Http = new XMLHttpRequest();
		Http.open("POST", this.backendDest + "/start");
		Http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
		Http.send(JSON.stringify(_data));
		console.log("Start request sent.");
		let _this = this;
		Http.onreadystatechange = function () {
			if (this.readyState == 4 && this.status == 200) {
				console.log(Http.responseText);
				_this.vue.notify(
					"Task",
					"success",
					"Task Done.",
					5000
				);
				_this.vue.startBtnLoading = false;
			}
		}
		this.interval = setInterval(this.getUpdates, 2500);
	}

	updateConfigTable() {
		this.vue.configTableData = [];
		for (let i = 0; i < this.configs.length; i++) {
			let group = this.configs[i].group, remarks = this.configs[i].remarks;
			this.vue.configTableData.push(
				{
					key: i,
					group: group,
					remarks: remarks,
					config: this.configs[i]
				}
			);
		}
	}

	readSubscription(_data) {
		this.vue.readSubBtnLoading = true;
		var Http = new XMLHttpRequest();
		Http.open("POST", this.backendDest + "/readsubscriptions");
		Http.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
		Http.send(JSON.stringify(_data));
		console.log("Read subscription request sent.");
		let _this = this;
		Http.onreadystatechange = function () {
			console.log(this.status);
			if (this.readyState == 4 && this.status == 200) {
				_this.vue.$notify({
					title: "Subscription",
					type: "success",
					message: "Read subscription success.",
					duration: 5000
				});
				_this.configs = JSON.parse(Http.responseText);
				_this.updateConfigTable();
				_this.vue.readSubBtnLoading = false;
			} else if (this.readyState == 4 && this.status == 0) {
				_this.vue.$notify({
					title: "Subscription",
					type: "error",
					message: "Read subscription failed.",
					duration: 5000
				});
				_this.vue.readSubBtnLoading = false;
				return;
			}
		}
	}

	updateTable() {
		for (let i = 0; i < this.diffResults.length; i++) {
			this.tableData.push(this.diffResults[i]);
		}
		this.diffResults = []
	}

	getDiff(newResults) {
		this.diffResults = [];
		for (let i = 0; i < newResults.length; i++) {
			let isFound = false;
			for (let j = 0; j < this.results.length; j++) {
				if (newResults[i].group === this.results[j].group
					&& newResults[i].remarks === this.results[j].remarks) {
					isFound = true;
					break;
				}
			}
			if (!isFound) {
				console.log(newResults[i].dspeed);
				this.diffResults.push({
					"group": newResults[i].group,
					"remarks": newResults[i].remarks,
					"loss": formatNumber(newResults[i].loss * 100, 2).toString() + "%",
					"ping": formatNumber(newResults[i].ping * 1000, 2) + "ms",
					"avgDSpeed": newResults[i].dspeed > 0 ? (formatNumber(newResults[i].dspeed / 1024 / 1024, 2) + "MB/s") : "N/A"
				});
			}
		}
		this.results = newResults;
	}

	getUpdates() {
		var resHttp = new XMLHttpRequest();
		resHttp.open("GET", this.backendDest + "/getresults");
		resHttp.send();
		let _this = this;
		resHttp.onreadystatechange = function () {
			let data;
			let results = [];
			if (this.readyState == 4 && this.status == 200) {
				try {
					data = JSON.parse(resHttp.responseText);
				} catch (e) {
					console.log(e);
					clearInterval(_this.interval);
				}
				if (data.status == "stopped") {
					_this.taskStatus = "Stopped";
					if (data.results.length > 0) {
						results = data.results;
						_this.getDiff(results);
						_this.updateTable();
					}
					clearInterval(_this.interval);
				} else {
					if (data.status == "pending") {
						console.log("Pending.");
						_this.taskStatus = "Pending";
					}
					if (data.status == "running") {
						_this.taskStatus = "Running";
						_this.current = data.current;
						if (data.results.length > 0) {
							results = data.results;
							_this.getDiff(results);
							_this.updateTable();
						}
					}
				}
			}
		}
	}
}

Vue.prototype.backend = new Backend();

new Vue({
	el: '#app',
	render: h => h(App)
})




