<template>
	<div id="app">
		<el-dialog title :visible.sync="initDialogShow" :lock-scroll="true">
			<el-card shadow="always">
				<h3>****** Import Hint ******</h3>
				<span>
					Before you publicly release your speed test results,
					<font
						color="#fff000"
					>be sure to ask the node owner if they agree</font> to the release to avoid unnecessary disputes.
					<br>Do not test the speed of the node
					<font color="#fff000">frequently</font>, avoiding a large burden on the node and affecting normal use.
				</span>
				<br>
				<h3>******  ******</h3>
				<span>
					
					<font color="#fff000"></font>,
					<br>
					<font color="#fff000"></font>
				</span>
			</el-card>
		</el-dialog>
		<div class="line"></div>
		<el-container>
			<el-main>
				<el-card shadow="always">
					<el-row :gutter="2">
						<el-col :span="20">
							<el-input
								v-model="subscriptionUrl"
								clearable
								@change="checkSubscriptionBtnAvailable"
								placeholder="Subscription URL"
							>
								<template slot="prepend">Subscription URL</template>
							</el-input>
						</el-col>
						<el-col :span="4">
							<el-button
								type="primary"
								:disabled="readSubsBtnDisabled"
								:loading="readSubBtnLoading"
								@click="readSubscription()"
							>Read</el-button>
						</el-col>
					</el-row>
					<br>
					<el-row>
						<el-col :span="4">
							<el-select
								v-model="proxyTypeValue"
								clearable
								@change="checkSubscriptionBtnAvailable"
								placeholder="Proxy Type"
							>
								<el-option
									v-for="item in proxyTypes"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								></el-option>
							</el-select>
						</el-col>
						<el-col :span="4">
							<el-select v-model="testMethodValue" clearable placeholder="Test Method">
								<el-option
									v-for="item in testMethods"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								></el-option>
							</el-select>
						</el-col>
						<el-col :span="4">
							<el-select v-model="testModeValue" clearable placeholder="Test Mode">
								<el-option
									v-for="item in testModes"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								></el-option>
							</el-select>
						</el-col>
						<el-col :span="4">
							<el-select v-model="sortValue" clearable placeholder="Result Sort">
								<el-option
									v-for="item in sortMethods"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								></el-option>
							</el-select>
						</el-col>
						<el-col :span="4">
							<el-select v-model="resultColorsValue" clearable placeholder="Colors">
								<el-option
									v-for="item in resultColors"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								></el-option>
							</el-select>
						</el-col>
						<el-col :span="4">
							<el-button
								type="primary"
								:disabled="!backendConnected"
								:loading="readColorsBtnLoading"
								@click="readColors()"
							>Update Colors</el-button>
						</el-col>
					</el-row>
					<br>
					<el-card>
						<el-row>
							<el-row>
								<el-col :span="6" :offset="18">
									<el-input
										clearable
										v-model="configTableFilterRemarks"
										size="mini"
										placeholder="Filter By Remarks"
									></el-input>
								</el-col>
							</el-row>
							<br>
							<el-table
								:data="configTableData.filter(data => !configTableFilterRemarks || data.remarks.toLowerCase().includes(configTableFilterRemarks.toLowerCase()))"
								border
								size="mini"
								height="400"
								empty-text="No Data"
								style="width: 100%"
								stripe
								@selection-change="handleConfigTableSelectionChanged"
							>
								<el-table-column type="selection" width="55" align="center"></el-table-column>
								<el-table-column sortable prop="group" label="Group" width="160" align="center"></el-table-column>
								<el-table-column sortable prop="remarks" label="Remarks" width="500" align="center"></el-table-column>
								<el-table-column label="Operation" width="120" align="center">
									<template slot-scope="scope">
										<el-button
											@click="deleteRow(scope.$index,configTableData)"
											size="small"
											type="danger"
										>Delete</el-button>
									</template>
								</el-table-column>
							</el-table>
						</el-row>
					</el-card>
					<br>
					<el-row></el-row>
					<div class="line"></div>
					<el-button type="primary" @click="showSettingsDialog" icon="el-icon-setting"></el-button>
					<el-button
						type="primary"
						@click="startTask"
						:loading="startBtnLoading"
						:disabled="!backendConnected"
					>Start</el-button>
					<el-button type="danger" @click="clearSubs">Clear Subscription</el-button>
					<el-button type="danger" @click="clearTable">Clear Result</el-button>
					<el-button type="info" @click="aboutDialogVisible = true">About</el-button>
				</el-card>
				<el-dialog title="Settings" :visible.sync="settingsDialogShow" :lock-scroll="true">
					<el-tabs v-model="settingsTabsActived" type="card" shadow="always">
						<el-tab-pane label="Backend Settings" name="1">
							<el-row>
								<el-input v-model="backendAddr">
									<template slot="prepend">Backend Address : </template>
								</el-input>
							</el-row>
							<br>
							<el-row>
								<el-input v-model="backendPort">
									<template slot="prepend">Backend Port : </template>
								</el-input>
							</el-row>
							<br>
							<el-row>
								<el-button @click="settingsDialogShow=false">Cancel</el-button>
								<el-button type="primary" @click="changeBackendDest">Confirm</el-button>
							</el-row>
						</el-tab-pane>
					</el-tabs>
				</el-dialog>
				<el-dialog title="SSR Speed Web UI" :visible.sync="aboutDialogVisible">
					<span>Web Version : {{ webVersion }}; Version : {{ mainVersion }}; Web Api Version : {{ webApiVersion }}</span>
					<br>
					<el-card shadow="always">
						<a href="https://github.com/NyanChanMeow/SSRSpeed">
							<el-button>Project : SSRSpeed</el-button>
						</a>
						<br>
						<br>
						<a href="https://github.com/NyanChanMeow/">
							<el-button>Author : NyanChan</el-button>
						</a>
						<br>
						<br>
						<a href="https://pay..site/">
							<el-button>Donation : </el-button>
						</a>
					</el-card>
					<div slot="footer" class="dialog-footer">
						<el-button type="primary" @click="aboutDialogVisible = false">Close</el-button>
					</div>
				</el-dialog>
				<br>
				<el-card shadow="always">
					<el-table
						:data="resultTableData"
						border
						stripe
						size="mini"
						empty-text="No Data"
						style="width: 100%"
					>
						<el-table-column sortable prop="group" label="Group" width="160" align="center"></el-table-column>
						<el-table-column sortable prop="remarks" label="Remarks" width="600" align="center"></el-table-column>
						<el-table-column sortable prop="loss" label="Packet Loss" width="130" align="center"></el-table-column>
						<el-table-column sortable prop="ping" label="TCP Ping" width="100" align="center"></el-table-column>
						<el-table-column sortable prop="avgDSpeed" label="AvgSpeed" width="130" align="center"></el-table-column>
					</el-table>
				</el-card>
			</el-main>
		</el-container>
		<el-footer></el-footer>
	</div>
</template>

<script>
export default {
	data() {
		return {
			webVersion: "0.2.1-beta",
			webApiVersion: "N/A",
			mainVersion: "N/A",
			settingsTabsActived: "1",
			settingsDialogShow: false,
			backendConnected: false,
			backendAddr: "",
			backendPort: 0,
			initDialogShow: false,
			aboutDialogVisible: false,
			configTableData: [],
			configTableFilterRemarks: "",
			configTableSelectedData: [],
			resultTableData: [],
			sortMethods: [
				{
					value: "SPEED",
					label: "By Speed"
				},
				{
					value: "REVERSE_SPEED",
					label: "By Speed (Reverse)"
				},
				{
					value: "PING",
					label: "By Ping"
				},
				{
					value: "REVERSE_PING",
					label: "By Ping (Reverse)"
				}
			],
			sortValue: "",
			testModes: [
				{
					value: "TCP_PING",
					label: "TCP Ping Only"
				},
				{
					value: "ALL",
					label: "Full Test (Default)"
				}
			],
			testModeValue: "",
			testMethods: [
				{
					value: "SOCKET",
					label: "Socket(Recommend)"
				},
				{
					value: "SPEED_TEST_NET",
					label: "SpeedTestNet"
				},
				{
					value: "FAST",
					label: "Fast.com"
				}
			],
			testMethodValue: "",
			proxyTypes: [
				{
					value: "SSR",
					label: "ShadowsocksR"
				},
				{
					value: "SSR-C#",
					label: "ShadowsocksR-C#"
				},
				{
					value: "SS",
					label: "Shadowsocks"
				},
				{
					value: "V2RAY",
					label: "V2Ray"
				}
			],
			proxyTypeValue: "",
			resultColors: [],
			resultColorsValue: "",
			subscriptionUrl: "",
			readSubsBtnDisabled: true,
			readColorsBtnLoading: false,
			readSubBtnLoading: false,
			startBtnLoading: false
		};
	},
	methods: {
		handleConfigTableSelectionChanged: function(selection) {
			this.configTableSelectedData = selection;
		},
		showSettingsDialog: function(){
			this.backendPort = this.backend.backendPort;
			this.backendAddr = this.backend.backendAddress;
			this.settingsDialogShow = true;
		},
		changeBackendDest: function(){
			this.backend.setBackendAddr(this.backendAddr,this.backendPort);
			this.settingsDialogShow = false
		},
		checkSubscriptionBtnAvailable: function() {
			if (this.subscriptionUrl != "" && this.proxyTypeValue != "") {
				this.readSubsBtnDisabled = false;
			} else {
				this.readSubsBtnDisabled = true;
			}
		},
		notify: function(title, type, msg, time) {
			this.$notify({
				title: title,
				type: type,
				message: msg,
				duration: time
			});
		},
		readColors: function() {
			this.backend.readColors();
		},
		readSubscription: function(url) {
			if (this.subscriptionUrl == "") {
				this.notify(
					"Read Subscription",
					"error",
					"Invalid Subscription URL.",
					5000
				);
				return;
			}
			if (this.proxyTypeValue == "") {
				this.notify("Read Subscription", "error", "Invalid Proxy Type", 5000);
				return;
			}
			let _data = {
				proxyType: this.proxyTypeValue,
				url: this.subscriptionUrl
			};
			this.backend.readSubscription(_data);
		},
		clearSubs: function() {
			this.configTableData = [];
			this.configTableFilterRemarks = "";
		},
		deleteRow: function(index, rows) {
			rows.splice(index, 1);
		},
		addRow: function(rows) {
			rows.push({});
		},
		showKeywordDialog: function() {
			this.keywordEditDialogVisible = true;
		},
		clearTable: function() {
			this.resultTableData = [];
		},
		startTask: function() {
			let configs = [];
			if (
				this.proxyTypeValue == "" ||
				this.testModeValue == "" ||
				this.testMethodValue == ""
			) {
				this.notify("Error", "error", "Invalid input.", 3000);
				return;
			}
			for (let ii = 0; ii < this.configTableSelectedData.length; ii++) {
				configs.push(this.configTableSelectedData[ii].config);
			}
			if (configs.length == 0) {
				this.notify("Error", "error", "No config", 3000);
				return;
			}
			let _data = {
				proxyType: this.proxyTypeValue,
				testMethod: this.testMethodValue,
				testMode: this.testModeValue,
				sortMethod: this.sortValue,
				colors: this.resultColors,
				configs: configs
			};
			this.backend.start(_data);
			this.$notify({
				title: "Task Started.",
				type: "success",
				message: "Task Started.",
				duration: 3000
			});
		}
	},
	created() {
		document.title = "SSR Speed Web UI " + this.webVersion;
		this.backend.init(this);
		this.backend.getVersion();
		this.initDialogShow = true;
		this.backendPort = this.backend.backendPort;
		this.backendAddr = this.backend.backendAddress;
	}
};
</script>

<style>
#app {
	font-family: Helvetica, sans-serif;
	text-align: center;
}
</style>



